.background_st{
    width: 100%;
  } 
  .compnent_wrapper_st {
    margin: 0 auto;
   max-width: 100%; 
   }
  .select_section_st {
    margin: 4px auto 4px;
    max-width: 100%; 
    background-color: #0E1119;
    border-radius: 4px;
  }
.button_st,
.button_flip {
  margin:4px 4px 16px;
  padding: 4px;
  transition: 0.75s;
  border-radius: 4px;
  font-family: 'Aldrich', sans-serif;
  box-shadow: 4px 4px 8px #fff;
  color: #fff;
  background-color: #23314D;
  border-top-left-radius: var(--radius);
  border-top-right-radius: var(--radius);
  box-shadow: var(--shadow);
}
.button_st:hover {
  color: #28C2E0;
  box-shadow: 6px 6px 8px #fff;
  background-color: #616B80;
}
.button_st:active {
  background-color: #fff;
  color: #28C2E0;
  box-shadow: 8px 8px 12px #28C2E0;
}


.active_st {
    background-color: #fff;
    color: #28C2E0;
    box-shadow: 8px 8px 12px #28C2E0;
  }
  
.second-img_st {
    visibility: hidden;
  }
  
.img-comp-overlay {
    visibility: hidden;
  }